import React from 'react'
import InstaAc from '../pics/instaAc.png'
import Insta from '../pics/insta.png'
import VkAc from '../pics/vkAc.png'
import Vk from '../pics/vk.png'
import TelegaAc from '../pics/telegaAc.png'
import Telega from '../pics/telega.png'
import YtAc from '../pics/ytAc.png'
import Yt from '../pics/yt.png'

export default function Contacts () {
    return (
      <div className="contacts">
        <div className="phone">+7 9999999999</div>
          <div className="contactList">
            <div className="elemnt">
              <a href="#">
              <img src={InstaAc} className="elementImg element-insta" alt="Instagram"></img>
              </a>
            </div>
            <div className="elemnt">
            <a href="#">
              <img src={VkAc} className="elementImg element-vk" alt="Vkontakte"></img>
            </a>
            </div>
            <div className="elemnt">
            <a href="#">
              <img src={TelegaAc} className="elementImg element-telega" alt="Telegram"></img>
              </a>
            </div>
            <div className="elemnt">
            <a href="#">
              <img src={YtAc} className="elementImg element-yt" alt="youtube"></img>
              </a>
            </div>
        </div>
      </div>
    )
}