import React from 'react'
import logo from '../pics/logo.png'
import Contacts from './Contacts'

export default function Container () {
    return (
        <div className="contnr">
          <div className="logo">
            <img src={logo} className="imgLogo" alt="photo1"></img>
          </div>
            <Contacts></Contacts>
        </div>
    )
}